        已选择<span class="active-num-style">2</span>个礼包
      </p>
      <van-button type="info" round>确认并返回</van-button>
    </footer>
  </div>
</template>

<script>
export default {
  components: {
    PackageListRow: () => import("./components/PackageListRow"),
  },
  data() {
    return {};
  },
  methods: {
    /**
     * 礼品列表
     * @param {String} username 用户名称
     * @param {String} phone 用户手机号
     * @param {String} password 用户密码
     */
    async giftDataList(username, phone, password) {
      const data = {
        username,
        phone,
        password,
      };
      const res = await this.$request("gift.giftDataList", data);
      console.log("res :>> ", res);
    },
  },
};
</script>

